import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
//import { TABS_DEMO_ROUTES } from './role-crud/routes';
//import { RoleCrudComponent } from './role-crud/role-crud.component';
import { UserRoleActivityComponent } from './user-role-activity.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    // {
    //     path : 'crud',component : RoleCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    // },
    {
        path : 'userRole-activityMapping',component : UserRoleActivityComponent,canActivate: [AuthGuard]
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class UserRoleActivityRoutingModule{}